@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&family=Nunito:wght@300;400;700&display=swap);
body {
    margin: 0;
    min-height: 100%;
    padding: 0;
    background-size: 100%;
    font-family: 'Lato';
  }

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  a {
    text-decoration: none;
  }



  .navbar {
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 25% 75%;
    align-items: center;
    background-image: linear-gradient(to right, rgb(0, 140, 255), rgb(0, 6, 34) );
    position: fixed;
    z-index: 1;
    top: 0;
    width: 100%;
    height: 120px; 
  }

  .navbar-home {
    justify-content: center;
    align-items: center;
    margin-left: 30px;
    padding: 10px 40px 10px 10px;
  }

  .navbar-home img {
    width: 60px;
    height: 60px;
  }

  .navbar-nav {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 0; 
  }

  .navlink {
    color: #edf1f2;
    font-size: 26px;
    padding-top: 35px;
    padding-bottom: 35px;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: 1.5em;
    transition: 0.5s;
    width: 100%;
  }

  .navlink:hover {
    background-color: #008299;
    text-decoration: underline;
    font-size: 28px;
  }




  /* RESPONSIVE RULES; */

  @media screen and (max-width: 1024px) {
    
    .navlink {
      font-size: 24px;
      margin-right: 20px;
    }
    .navlink:hover {
      font-size: 26px;
    }
  }

  @media screen and (max-width: 768px) {

    .navbar {
      grid-template-columns: 100px auto;
    }

    .navbar-home {
      justify-content: center;
      margin-left: 10px;
      padding: 10px;
    }

    .navlink {
      font-size: 22px;
      padding: 10px;
      margin-bottom: 10px; 
    }

    .navlink:hover {
      font-size: 24px;
    }

    .navbar-nav {
      flex-wrap: wrap;
      justify-content: flex-end;
      align-items: center;
      display: grid;
      grid-template-columns: 140px 140px;
      grid-row-gap: 20px;
      }
  }

  @media screen and (max-width: 470px) {
    .navbar {
      grid-template-columns: auto 140px;
    }
    .navlink {
      font-size: 20px;
      line-height: 1.3;
    }
    .navlink:hover {
      font-size: 20px;
      background-color: transparent;
    }
    .navbar-nav {
      display: block;
      flex-wrap: wrap;
      justify-content: flex-end;
    }
    .navbar-home {
      margin-left: 5px;
    }
    .navbar-home img {
      width: 50px;
      height: 50px;
    }
  }

  /* @media screen and (max-width: 360px) {
    .navbar {
      grid-template-columns: auto 150px;
    }
    .navlink {
      font-size: 18px;
    }
    .navbar-nav {
      flex-wrap: wrap;
      justify-content: space-evenly;
    }
  } */

.video-container {
    background-color: #b9f2fc;
    width: 100%;
    height: 100%;
    border-top: solid 5px darkblue;
}

.video-text {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    font-family: 'Lato';
    line-height: 1.5;
    padding-top: 5px;
    padding-bottom: 0px;
    padding-left: 10px;
    padding-right: 10px;
}

  .player-wrapper {
    position: relative;
    padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
  }
   
  .react-player {
    position: absolute;
    top: 5%;
    left: 9%;
    width: 80%;
    height: 80%;

    border: solid 2px black;
    box-shadow: 0 15px 15px 0 #00000033;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    
    
  }


header {
  height: 100vh;
  background: 
  linear-gradient(
    rgba(0, 0, 0, 0.5),
    rgba(0, 0, 0, 0.5)
  ), url(/react-dunbar-pages/static/media/dunbaraerial.bc9fde7b.jpg);
  background-position: center; 
  background-repeat: no-repeat; 
  background-size: cover;
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 3em;
}

.title {
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
  text-align: center;  
}

h1 {
  text-transform: uppercase;
  margin: 0;
  font-size: 82px;
  white-space: nowrap;
  font-weight: bold;
}

h2 {
    margin: 0;
    font-size: 20px;
    padding-top: 3em;
  }

.home-information {
  margin: 3em;
}

.homepage-body-text {
  line-height: 1.5;
  font-size: 22px;
  text-align: justify;
  font-family: 'Nunito';
}

.nav {
  display: grid;
  grid-template-columns: 1fr;
  align-items: center;
  justify-self: center;
  width: 100%;
}

.menu-banner-style {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  list-style-type: none;
  margin-bottom: 50px;

}

.menu-home {
  box-shadow: 0 2px 2px 0 #00000033;
  max-width: 300px;
  text-align: center;
  padding: 0.5em;
  background-color: #f8f8ff;
  transition: 0.5s;
  margin: 0.5em;

}

.menu-home:hover {
  background-color: #b9f2fc;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.menu-home img {
  width: 270px;
  height: 200px;
  border: 1px solid black;
  border-radius: 20px 50px;
  padding: 10px;
  margin-left: auto;
  margin-right: auto;
  
}

.menu-title-home {
  background-color: black;
  color: white;
  font-size: 24px;
  font-family: "Nunito";
  padding: 0.3em;
  border: solid white;
  margin-top: 0.5em;
}


/* RESPONSIVE RULES; */

@media screen and (max-width: 1024px) {
  .title {
    top: 40%;  
  }
  h1 {
    font-size: 66px;
  }
  header{
    height: 65vh;
  }
  .menu-home {
    max-width: 220px;
    padding: 0.2em;
    margin: 0.5em;
  }

  .menu-home img {
    width: 200px;
    height: 150px;  
  }

  .menu-title-home {
    font-size: 18px;
    font-family: "Nunito";
    padding: 0.1em;
    border: solid white;
    margin-top: 0.2em;
  }
}

@media screen and (max-width: 768px) {
  h1 {
    font-size: 36px;
  }
  header{
    height: 60vh;
  }
  .menu-home {
    margin: 1em;
  }
}

@media screen and (max-width: 415px) {
  
  h1 {
    font-size: 30px;
  }
  h2 {
    font-size: 18px;
  }
  .homepage-body-text {
    line-height: 1.5;
    font-size: 18px;
  }
  .home-information {
    margin: 1em;
  }
}

@media screen and (max-width: 360px) {
  .title {
    top: 50%;  
  }
  h1 {
    font-size: 26px;
  }
  h2 {
    font-size: 16px;
  }
  .homepage-body-text {
    line-height: 1.5;
    font-size: 16px;
  }
}

  .footer {
    background-color: #00144b;
    background-image: linear-gradient(to right, rgb(0, 140, 255), rgb(0, 6, 34) );
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
    /* margin-top: 50px; */
  }

  .footer-nav {
    max-width: 100%;
    height: 100px;
    display: flex;
    flex-wrap: wrap;
    justify-content: left;
    align-items: center;
    margin-left: 50px;
    
  }

  .social-footer, img {
    height: 50px;
    width: 50px;
    margin: 15px; 
  }

  .footer-text { 
    color: #c1c3c4;
    font-size: 20px;
    font-weight: bold;
    justify-self: right;
    align-items: center;
    margin-right: 125px;
    
  }

  /* RESPONSIVE RULES */
  
  @media screen and (max-width: 768px) {
    
    .social-footer, img {
      height: 40px;
      width: 40px;
    }
    .footer-nav {
      margin-left: 30px; 
    }
    .footer-text { 
      font-size: 18px;
      margin-right: 80px;
    }
  }

  @media screen and (max-width: 620px) {
    
    .social-footer, img {
      height: 36px;
      width: 36px;
      margin: 10px;
    }
    .footer-nav {
      margin-left: 15px; 
    }
    .footer-text { 
      font-size: 16px;
      margin-right: 50px;
    }
  }

  @media screen and (max-width: 480px) {
    
    .social-footer, img {
      height: 25px;
      width: 25px;
      margin: 8px;
    }
    .footer-nav {
      margin-left: 15px; 
    }
    .footer-text { 
      font-size: 14px;
      margin-right: 50px;
    }
  }

  @media screen and (max-width: 360px) {
    
    .social-footer, img {
      margin: 4px;
    }
    .footer-nav {
      margin-left: 10px; 
    }
  }
.lister-main-header {
    justify-self: start;
    margin-top: 3em;
    margin-left: 1em;
    font-size: 48px;
  }

  .lister-body-container {
    margin: 2em;
    margin-left: 3em;
  }

  .lister-body-text {
    line-height: 1.5;
    font-size: 20px;
    text-align: justify;
    font-family: 'Lato';
  }

  .lister-banner {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;
    justify-self: center;
    width: 100%;
  }
  
  .lister-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    list-style-type: none;
  
  }
  
  .lister-item {
    box-shadow: 0 4px 4px 0 #00000033;
    max-width: 360px;
    height: 600px;
    text-align: center;                  
    padding: 1em;
    margin: 1.5em;
    background-color: #e7e7ff;
  }
  
  .lister-item img {
    width: 260px;
    height: 180px;
    border: 1px solid black;
    border-radius: 20px 50px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 1em;
    padding: 0;
  }
  
  .lister-title {
    color: #555454;
    font-size: 20px;
    font-family: "Nunito";
    padding-top: 1.75em;
    text-align: center;
  }

  .lister-detail {
    text-align: center;                  
    font-size: 18px;
  }

  .link-lister {
    border: 1px solid black;
    background-color: #00144b;
    color: #ffffff;
    padding: 0.2em;
    padding-left: 1em;
    padding-right: 1em;
    font-size: 17px;
    font-family: "Nunito";
    cursor: pointer;
    text-decoration: none;
    border-radius: 5px;
    
  }
  
  .link-lister:hover {
    background-color: #ffffff;
    color: #00144b;
  }

  .map-link {
    border: 1px solid black;
    background-color: #00144b;
    color: #ffffff;
    padding: 0.2em;
    padding-left: 1em;
    padding-right: 1em;
    font-size: 17px;
    font-family: "Nunito";
    cursor: pointer;
    text-decoration: none;
    border-radius: 5px;
    
  }
  
  .map-link:hover {
    background-color: #ffffff;
    color: #00144b;
  }

   /* RESPONSIVE RULES */

   @media screen and (max-width: 768px) {
    
    .lister-item {
      max-width: 320px;             
      padding: 0.5em;
      margin: 0.5em;
    }

    .lister-title {
      font-size: 17px;
      padding-top: 1em;
    }

    .lister-detail {              
      font-size: 17px;
    }
  }

  @media screen and (max-width: 415px) {

    .lister-main-header {
      margin-top: 150px;
      font-size: 30px;
      margin-left: 0.5em; 
    }
    .lister-body-container {
      margin: 1em;
    }
  
    .lister-body-text {
      font-size: 18px;
    }
  }

  @media screen and (max-width: 360px) {

    .lister-main-header {
      font-size: 24px;
      margin-left: 0.5em; 
    }
  }

 



  

  
